<template>
  <div style="height:100%">
    <div class="Input" style="height:45%">
    <div style="margin:1%">
      <span style="position: relative;right:1%;font-size:0.2rem;font-weight:bold">Input your text to summarize:</span>
      <span style="position: relative;right:-9%;font-size:0.2rem;font-weight:bold">Input your golden summary:</span>
    </div>
    <div style="margin:0.5%">
      <span class="inputText">
        <textarea placeholder="input" style="width:40%; border:solid 1px #d8d8d8; border-radius:20px; font-size:0.13rem; padding:10px; background:transparent; color:white; outline:none; line-height:17px" rows = "10" @input="descInput" v-model="inputText" /></span>
        <span class="numberV" style="font-size:0.12rem">{{inputVal}}&ensp;&ensp;&ensp;</span>
      <span class="gold_summary">
        <textarea placeholder="gold_summary" style="width:20%; border:solid 1px #d8d8d8; border-radius:20px; font-size:0.13rem; padding:10px; background:transparent; color:white; outline:none;line-height:17px" rows = "10" @input="descInput" v-model="gold_summary" /></span>
        <span class="numberV" style="font-size:0.12rem">{{goldVal}}</span>
    </div>
    <div>
    <div class="number" style="margin:0.5%">
          <span style="font-size:0.16rem;position:relative;right:20%">{{min_length}}</span>
          <span style="font-size:0.16rem">{{max_length}}</span>
          <span style="font-size:0.16rem;position:relative;left:20%">{{num_beams}}</span>
    </div>
    <div class="bar" style="margin:0.5%">
          <input id = "min_length" type = "range" v-model="min_length" min="0" max="200" @input="MinStyle" style = "position:relative;right:12%">
          <input id = "max_length" type = "range" v-model="max_length" min="0" max="500" @input="MaxStyle">
          <input id = "num_beams" type = "range" v-model="num_beams" min="0" max="10" @input="NumStyle" style = "position:relative;right:-12%">
    </div>
    <div class="label" style="margin:0.5%">
          <span style="font-size:0.16rem;position:relative;right:14%">min_length</span>
          <span style="font-size:0.16rem;position:relative;right:-0.5%">max_length</span>
          <span style="font-size:0.16rem;position:relative;right:-15%">num_beams</span>
    </div>
      </div>
    </div>
    <div class="Button" style="margin:1%;height:5%">
      <button id = "generate" style = "cursor:pointer; position: relative;color: rgba(255, 255, 255, 1);;background-color: rgba(219, 87, 51, 1);;display: block;padding: 4px;border-radius: 8px;box-shadow: 0px 9px 0px rgba(219, 31, 5, 1), 0px 9px 25px rgba(0, 0, 0, .7);margin:auto;width: 60%;text-align: center;outline:none;border-color:rgba(219, 87, 51, 1);font-size:0.18rem" v-on:click="generate()">Click here to generate</button>
    </div>
    <div class="Output" style="margin:1%;height:50%">
      <div style="margin:1.5%;height:10%">
      <span style="  position: relative;right:9%;font-size:0.2rem;font-weight:bold">Generated summarization:</span>
      <span style="  position: relative;right:-12%;font-size:0.2rem;font-weight:bold">ROUGE:</span>
    </div>
    <div style="height:50%">
      <textarea style="position: relative;right:12%;width:40%; border:solid 1px #d8d8d8; border-radius:20px; font-size:0.13rem; padding:10px; background:transparent; color:white;outline:none;line-height:17px" rows = "10" v-model="outputText" readonly />
      <span class="numberV"style="position: relative;right:12%;font-size:0.12rem">{{resultVal}}</span>
    </div>
      <el-table :data="tableData" style="position: relative;top:-55%;right:-61%;background:transparent;width:25%;font-size:0.12rem;height:80%;" min-width="20%">
        <el-table-column prop="metrics" label="Metrics" min-width="5%" show-overflow-tooltip></el-table-column>
        <el-table-column prop="f" label="F" min-width="5%" show-overflow-tooltip></el-table-column>
        <el-table-column prop="p" label="P" min-width="5%" show-overflow-tooltip></el-table-column>
        <el-table-column prop="r" label="R" min-width="5%" show-overflow-tooltip></el-table-column>
      </el-table>
    </div>
  </div>
</template>
<style>
p{
  margin: 5px;
  font:sans-serif
}

input[type=range]{ 
     margin-top: 5px;
      outline: none; 
     -webkit-appearance: none;/*清除系统默认样式*/  
     width:10% !important;  
     background: -webkit-linear-gradient(#61bd12, #61bd12) no-repeat, #ddd;  
     background-size: 30% 100%;
    height: 3px;/*横条的高度*/  
} 
 /*拖动块的样式*/  
   input[type=range]::-webkit-slider-thumb {  
   -webkit-appearance: none;/*清除系统默认样式*/  
  height:16px;/*拖动块高度*/  
  width: 16px;/*拖动块宽度*/  
  background: #fff;/*拖动块背景*/  
  border-radius: 50%; /*外观设置为圆形*/  
   border: solid 1px #ddd; /*设置边框*/  
} 
.el-table th, .el-table tr{
  border: 0;
  background-color: transparent;
}
.el-table {
  color:white;
}
.el-table thead {
   color: white;
   font-weight: bold;
   }
.el-table tbody tr:hover>td { 
    background-color:#367f7f78 !important
}
.el-table::before {
left: 0;
bottom: 0;
width: 100%;
height: 0px;
}
</style>
<script>
import axios from "axios"

  export default{
    name : "first",
      data () {
        return {
            inputVal: 0,
            resultVal: 0,
            goldVal : 0,
            inputText:"",
            outputText:"",
            max_length:100,
            min_length:60,
            gold_summary:"",
            num_beams:4,
            tableData:[{"metrics":"rouge-1","f":"","p":"","r":""},
            {"metrics":"rouge-2","f":"","p":"","r":""},
            {"metrics":"rouge-l","f":"","p":"","r":""},
            ]
        }
      },
      methods: {
        MinStyle(){
          var minstyle= document.getElementById("min_length");
          minstyle.style["background-size"] = this.min_length/2+"% 100%";
        },
        notnull(str){
          return str!="";
        },
        MaxStyle(){
          var maxstyle= document.getElementById("max_length");
          maxstyle.style["background-size"] = this.max_length/5+"% 100%";
        },
        NumStyle(){
          var numstyle= document.getElementById("num_beams");
          numstyle.style["background-size"] = this.num_beams*10+"% 100%";
        },
        descInput(){
            this.inputVal = this.inputText.trim().split(/\s+/).filter(this.notnull).length;
            this.goldVal = this.gold_summary.trim().split(/\s+/).filter(this.notnull).length;
          },
          generate(){
            console.log(this.outputText);
            var button = document.getElementById("generate");
            button.style["box-shadow"] = "0px 3px 0px rgba(219, 31, 5, 1), 0px 3px 6px rgba(0, 0, 0, .9)";
            let data = {
              "content":this.inputText,
              "min_length":this.min_length,
              "max_length":this.max_length,
              "num_beams":this.num_beams,
              "gold_summary":this.gold_summary
            };
            console.log(data);
            axios.post("/snippet/",data).then(res=>{
              console.log(res)
              this.outputText = res.data.abstract;
              this.resultVal = this.outputText.trim().split(/\s+/).length;
              this.tableData[0]["f"] = res.data.rouge[0]["rouge-1"]["f"].toFixed(3);
              this.tableData[0]["p"] = res.data.rouge[0]["rouge-1"]["p"].toFixed(3);
              this.tableData[0]["r"] = res.data.rouge[0]["rouge-1"]["r"].toFixed(3);
              this.tableData[1]["f"] = res.data.rouge[0]["rouge-2"]["f"].toFixed(3);
              this.tableData[1]["p"] = res.data.rouge[0]["rouge-2"]["p"].toFixed(3);
              this.tableData[1]["r"] = res.data.rouge[0]["rouge-2"]["r"].toFixed(3);
              this.tableData[2]["f"] = res.data.rouge[0]["rouge-l"]["f"].toFixed(3);
              this.tableData[2]["p"] = res.data.rouge[0]["rouge-l"]["p"].toFixed(3);
              this.tableData[2]["r"] = res.data.rouge[0]["rouge-l"]["r"].toFixed(3);
              button.style["box-shadow"] = "0px 9px 0px rgba(219, 31, 5, 1), 0px 9px 25px rgba(0, 0, 0, .7)";
            })
          },
          //restrict to number
      }
  }
</script>



