<template>
  <div id="app" :style="backgroundImg">
    <div>
    <p style="font-size:0.25rem;font-weight:bold">AutoAbstracter</p>
  </div>
    <router-view/>
  </div>
</template>

<style>

#app {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: white;
}
  *{
    margin: 0;
    padding: 0;
  }
</style>
<script>
  export default{
     name: 'App',
     data(){
      return {
         backgroundImg:{
              backgroundImage:"url("+require("@/assets/background.jpg")+")",
              backgroundRepeat: "no-repeat",
              backgroundSize: "100% 100%",
              position:"fixed",
              height:"100%",
              width:"100%",
            },
        }
    }
   }
</script>



